import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { UbccEventBusService } from '../../../service/event-bus-service';

@Component({
    selector: 'lib-dimension-input-control',
    templateUrl: './dimension-input-control.component.html',
    styleUrls: ['./dimension-input-control.component.css'],
    providers:[UbccEventBusService]
})
export class DimensionInputControlComponent implements OnInit {

    @Input() dimensionItem: any;

    constructor(private ebsService: UbccEventBusService) { }

    ngOnInit() {
        this.dimensionItem = this.dimensionItem || {};
    }

    ngModelChangeHandler($event){
        this.ebsService.ebs.emit(this.dimensionItem);
    }

}
