import { Component, OnInit, Input, ViewChild, ViewContainerRef, OnDestroy, ComponentFactoryResolver, ChangeDetectorRef, Type, Injector } from '@angular/core';
import { UbccParamTemplate, UbccParamTemplateAssignment, UbccParamTemplateVo, UbccParamTemplateSerializer, DataItem, DataTypeExtensionConst, StructureTypeRef, HelpType, StructureTypeRefEnum, UbccParamTemplateAssignmentSerializer } from '@gsp-cmp/ubcc-param-vo';
import { UbccParamDataManangerService } from './service/http-service';
import { UbccParamDataUiComponent } from './ubcc-param-data-ui/ubcc-param-data-ui.component';
import { UbccParamDimensionUiRootComponent } from './ubcc-param-dimension-ui-root/ubcc-param-dimension-ui-root.component';
import { Parameter, DataTypeConst, IDataType, CollectionType, PrimitiveType, PrimitiveTypeKind, DataType } from '@ecp-caf/common-structure';
import { BaseParamData } from './common/parameter-data/base-param-data';
import { DimensionItemData } from './common/dimension-item-data/dimension-item-data';
import { UbccEventBusService } from './service/event-bus-service';
import { UbccParamDataUtils } from './utils/param-data-utils';

@Component({
    selector: 'lib-ubcc-param-data-ui-root',
    templateUrl: './ubcc-param-data-ui-root.component.html',
    styleUrls: ['./ubcc-param-data-ui-root.component.css'],
    providers: [UbccEventBusService]
})
export class UbccParamDataUiRootComponent implements OnInit, OnDestroy {

    private _assignment: UbccParamTemplateAssignment;

    @Input() set assignment(assignment: UbccParamTemplateAssignment) {
        this._assignment = assignment;
        this.loadData();
    }

    _canEdit:boolean=true;

    @Input() set canEdit(canEdit:boolean){
        this._canEdit=canEdit;
        if(this.currentInstance){
            this.currentInstance.canEdit=this._canEdit;
        }
    }

    template: UbccParamTemplate;

    dimensionItemDatas: Array<DimensionItemData> = [];

    paramDataItems: Array<Parameter> = [];

    paramDataItemsClone: Array<Parameter> = [];

    currentInstance:any;

    templateSerializer=new UbccParamTemplateSerializer();
    
    assignmentSerializer=new UbccParamTemplateAssignmentSerializer();

    @ViewChild('paramDataPanel', { read: ViewContainerRef }) paramDataPanel: ViewContainerRef;

    constructor(
        private utils:UbccParamDataUtils,
        private ebsService: UbccEventBusService,
        private http: UbccParamDataManangerService,
        private resolver: ComponentFactoryResolver) {
        this.ebsService.ebs.subscribe(event => {
            this.loadDimensionDataFromRemote();
        })
    }

    ngOnInit() {
        this._assignment = this._assignment || new UbccParamTemplateAssignment();
        this.template = this.template || new UbccParamTemplate();
    }

    ngOnDestroy(): void {
        this.paramDataPanel.clear();
    }

    private loadData() {
        //åæ°,ç»´åº¦æ°æ®æºåå§å
        this.dimensionItemDatas=[];
        this.paramDataItems=[];
        this.paramDataItemsClone=[];
        if (!this._assignment || !this._assignment.templateId) {
            return;
        }
        this.http.getParamTemplate(this._assignment.templateId).subscribe(
            (vo: UbccParamTemplateVo) => {
                if (!vo||!vo.content) {
                    return;
                }
                let serializer = new UbccParamTemplateSerializer();
                this.template = serializer.deserializer(JSON.parse(vo.content));
                this.load();
            },
            err=>{
                throw err;
            }
        )
    }

    private load() {
        //åæ°ç»æåå§åï¼åæ°å¼ä¸ºç©º
        this.paramDataItemsInit();
        //æ ¹æ®ç»´åº¦åå»ºå¯¹åºçUIç»ä»¶
        if (this._assignment.useDimension) {
            //ç»´åº¦ç»æåå§åï¼ç»´åº¦æ°æ®ä¸ºç©º
            this.dimensionDataItemsInit();
            //åå»ºç»´åº¦UIç»ä»¶ï¼æ­¤å¤ä¸åæ°ï¼åªæ¸²æçé¢ï¼å¨éæ©ç»´åº¦æ°æ®ååå è½½æ°æ®
            this.createDimensionRootUiComponent();
        }
        else {
            this.http.getParamData(this._assignment.templateId, this._assignment.bizTypeId).subscribe(
                (dataItems: Array<DataItem>) => {
                    this.getParamValues(dataItems);
                },
                err=>{
                    throw err;
                }
            );
             //åå»ºåæ°UIç»ä»¶
            this.createParamDataUiComponent();
        }
    }

    private getParamValues(dataItems: Array<DataItem>) {
        if (!dataItems || dataItems.length <= 0) {
            return;
        }
        this.paramDataItems.map((paramDataItem: BaseParamData) => {
            let data = dataItems.find(item => item.dataCode === paramDataItem.code);
            if (data) {
                paramDataItem.value = data.dataValue;
                if (!this.utils.isHelpType(paramDataItem.parameterType)) {
                    return;
                }
                paramDataItem.valueText = data.dataValueText||'';
                // ä»åç«¯è·åå¸®å©æ°æ®
                // this.getHelpData(data,paramDataItem);
            }
        });
        this.paramValuesClone(this.paramDataItemsClone,this.paramDataItems);
    }

    private paramValuesClone(target:any[],source:any[]) {
        if(source.length==0||target.length==0||target.length!==source.length){
            return;
        }
        target.map(paramDataItemClone => {
            let index = source.findIndex(x => x.code === paramDataItemClone.code);
            if(index!==(-1)){
                paramDataItemClone.id = source[index].id;
                paramDataItemClone.value = source[index].value;
                paramDataItemClone.valueText=source[index].valueText;
            }
        });
    }

    private loadDimensionDataFromRemote() {
        this.paramDataItemsInit();
        this.currentInstance.paramDataItems = this.paramDataItems;
        let dimensionItemValues = [];
        this.dimensionItemDatas.map((x: DimensionItemData) => dimensionItemValues.push(x.value));
        let count=dimensionItemValues.length;
        if(count<=0){
            return;
        }
        if(count===1){
            if(!dimensionItemValues[0]){
                return;
            }
        }
        if(count===2){
            if(!dimensionItemValues[0]||!dimensionItemValues[1]){
                return;
            }
        }
        let dimension1=dimensionItemValues[0]?dimensionItemValues[0]:null;
        let dimension2=dimensionItemValues[1]?dimensionItemValues[1]:null;
        this.http.getDimensionData(this._assignment.templateId, 
            this._assignment.bizTypeId,
            dimension1,dimension2,count).subscribe(
            (dataItems: Array<DataItem>) => {
                this.getParamValues(dataItems);
            }
        )
    }

    private paramDataItemsInit() {
        let template=this.templateSerializer.deserializer(this.templateSerializer.serialize(this.template));
        this.paramDataItems = [];
        if (template&& template.params) {
                template.params.map(x=>{
                    if(this._assignment.useDimension){
                        this.dimensionParamDefaultValueInit(x as BaseParamData);
                    }
                    else{
                        (x as BaseParamData).value=x.defaultValue;
                    }
                    this.paramDataItems.push(x);
                }
            );
        }
        this.paramDataItemsCloneInit();
    }

    private dimensionParamDefaultValueInit(paramData:BaseParamData){
        let count=this.dimensionItemDatas.length;
        if(count<=0){
            return;
        }
        if(count===1){
            if(!this.dimensionItemDatas[0]||!this.dimensionItemDatas[0].value){
                return;
            }
        }
        if(count===2){
            if(!this.dimensionItemDatas[0]||!this.dimensionItemDatas[1]||!this.dimensionItemDatas[0].value||!this.dimensionItemDatas[1].value){
                return;
            }
        }
        paramData.value=paramData.defaultValue;
    }

    private paramDataItemsCloneInit() {
        let template=this.templateSerializer.deserializer(this.templateSerializer.serialize(this.template));
        this.paramDataItemsClone = [];
        if (template&& template.params) {
                template.params.map(x=>{
                    if(this._assignment.useDimension){
                        this.dimensionParamDefaultValueInit(x as BaseParamData);
                    }
                    else{
                        (x as BaseParamData).value=x.defaultValue;
                    }
                    this.paramDataItemsClone.push(x);
                }
            );
        }
    }

    private dimensionDataItemsInit() {
        let assignment=this.assignmentSerializer.deserialize(this.assignmentSerializer.serialize(this._assignment));
        if (assignment&&assignment.dimensionInfo
            && assignment.dimensionInfo.definition
            && assignment.dimensionInfo.definition.dimensionItems) {
            this.dimensionItemDatas = [];
            assignment.dimensionInfo.definition.dimensionItems.forEach(dimensionData => {
                this.dimensionItemDatas.push(dimensionData as DimensionItemData);
            });
        }
    }

    private createParamDataUiComponent() {
        this.paramDataPanel.clear();
        const factory = this.resolver.resolveComponentFactory(UbccParamDataUiComponent);
        let cmpRef = this.paramDataPanel.createComponent(factory);
        cmpRef.instance.canEdit = this.canEdit;
        cmpRef.instance.template = this.template;
        cmpRef.instance.paramDataItems = this.paramDataItems;
        this.currentInstance=cmpRef.instance;
    }

    private createDimensionRootUiComponent() {
        this.paramDataPanel.clear();
        const factory = this.resolver.resolveComponentFactory(UbccParamDimensionUiRootComponent);
        let cmpRef = this.paramDataPanel.createComponent(factory);
        cmpRef.instance.canEdit = this.canEdit;
        cmpRef.instance.dimensionItemDatas = this.dimensionItemDatas;
        cmpRef.instance.paramDataItems = this.paramDataItems;
        cmpRef.instance.template = this.template;
        this.currentInstance=cmpRef.instance;
    }

    convertDimensionDataToDataVo() {
        if (!this.dimensionItemDatas) {
            return null;
        }
        let dimensionValues = new Array<DataItem>();
        this.dimensionItemDatas.map(x => {
            let dimensionValue = new DataItem();
            dimensionValue.dataCode = x.code;
            dimensionValue.dataValue = (x as DimensionItemData).value;
            dimensionValue.dataType = this.getDimensionValueType(x.dataType);
            dimensionValues.push(dimensionValue);
        });
        return dimensionValues;
    }

    convertParamDataToDataVo() {
        if (!this.paramDataItems) {
            return null;
        }
        let paramValues = new Array<DataItem>();
        this.paramDataItems.map(x => {
            let paramValue = new DataItem();
            paramValue.dataCode = x.code;
            paramValue.dataValue = (x as BaseParamData).value;
            paramValue.dataType = this.getParamValueType((x as BaseParamData).parameterType);
            paramValues.push(paramValue);
        });
        this.paramValuesClone(this.paramDataItemsClone,this.paramDataItems);
        return paramValues;
    }

    private getDimensionValueType(dataType: IDataType) {
        let kind = dataType.getKind();
        switch (kind) {
            case DataTypeConst.DEFAULT_PRIMITIVE:
                return `${DataTypeConst.DEFAULT_PRIMITIVE}.${(dataType as PrimitiveType).primitiveTypeKind}`;
            case DataTypeConst.DEFAULT_ENUMERATION:
                return DataTypeConst.DEFAULT_ENUMERATION;
            case DataTypeExtensionConst.HELP_TYPE:
                return DataTypeExtensionConst.HELP_TYPE+`##${(dataType as HelpType).helpId}`;
            default:
                throw new Error(`ä¸å¡åæ°ä¸æ¯æçåæ°ç±»å${kind}`);
        }
    }

    private getParamValueType(dataType: IDataType) {
        let kind = dataType.getKind();
        switch (kind) {
            case DataTypeConst.DEFAULT_COLLECTION:
                let elemetType = (dataType as CollectionType).elementType;
                return `${DataTypeConst.DEFAULT_COLLECTION}.` + this.getParamValueType(elemetType);
            case DataTypeConst.DEFAULT_PRIMITIVE:
                return `${DataTypeConst.DEFAULT_PRIMITIVE}.${PrimitiveTypeKind[(dataType as PrimitiveType).primitiveTypeKind]}`;
            case DataTypeExtensionConst.UBCC_STRUCTURE_TYPE_REF:
                let refType = dataType as StructureTypeRef;
                return refType.type === StructureTypeRefEnum.HELP ? DataTypeExtensionConst.HELP_TYPE +`##${refType.refId}`: DataTypeConst.DEFAULT_ENUMERATION;
            default:
                throw new Error(`ä¸å¡åæ°ä¸æ¯æçåæ°ç±»å${kind}`);
        }
    }

    cancel(){
        this.paramValuesClone(this.paramDataItems,this.paramDataItemsClone);
        this.currentInstance.paramDataItems=this.paramDataItems;
    }
}
