import { Component, OnInit, Output, EventEmitter, Input, TemplateRef, ViewChild } from '@angular/core';
import { UbccParamTemplate, UbccParamTemplateVo, UbccParamTemplateSerializer, StructureTypeRef } from '@gsp-cmp/ubcc-param-vo';
import { BsModalRef } from '@farris/ui-modal';
import { UbccParamDataManangerService } from '../service/http-service';
import { Parameter, DataTypeConst } from '@ecp-caf/common-structure';
import { BaseParamData } from '../common/parameter-data/base-param-data';
import { HelpDataItem } from '../common/help-data-item/help-data-item';
import { UbccParamDataUtils } from '../utils/param-data-utils';

@Component({
    selector: 'lib-ubcc-param-data-ui-dialog',
    templateUrl: './ubcc-param-data-ui-dialog.component.html',
    styleUrls: ['./ubcc-param-data-ui-dialog.component.css']
})
export class UbccParamDataUiDialogComponent implements OnInit {

    template: UbccParamTemplate;

    _data: any;

    @Input() templateId: string;

    /**
     * è¾å¥æ°æ®ä¸ºJSONå¯¹è±¡
     */
    @Input() set data(data: any) {
        this._data = data;
        this.loadParamData(this.templateId);
    }

    paramDataItems: Array<Parameter> = [];

    @Output() confirmEmitter: EventEmitter<any> = new EventEmitter<any>();

    @Output() closeEmitter: EventEmitter<any> = new EventEmitter<any>();

    @ViewChild('btnRef') btnRef: TemplateRef<any>;

    dialog: BsModalRef;

    constructor(
        private utils:UbccParamDataUtils,
        private http: UbccParamDataManangerService) { }

    ngOnInit() {
        this.template = this.template || new UbccParamTemplate();
    }

    private loadParamData(templateId: string) {
        if (!templateId) {
            return;
        }
        this.http.getParamTemplate(templateId).subscribe(
            (vo: UbccParamTemplateVo) => {
                if (!vo.content) {
                    return;
                }
                let serializer = new UbccParamTemplateSerializer();
                this.template = serializer.deserializer(JSON.parse(vo.content));
                this.getParamDataItemsStructure();
                this.convertJsonToParamData();
            }
        )
    }

    private convertJsonToParamData() {
        if (!this._data || Object.keys(this._data).length <= 0) {
            return;
        }
        this.paramDataItems.map((item: BaseParamData) => {
            let key = Object.keys(this._data).find(x => x === item.code);
            if (!key || !this._data[key]) {
                return;
            }
            if (this.isCollection(item)) {
                //åæ°çå¼å¦ææ¯æ°ç»ï¼ä»¥éå·éå¼
                item.value = this._data[key].join(',');
                if (!this.utils.isHelpType(item.parameterType)) {
                    return;
                }
                let helpId = (this.utils.getCollectionElementType(item.parameterType) as StructureTypeRef).refId;
                this.http.getHelpData(helpId, this._data[key]).subscribe(
                    (result: Array<HelpDataItem>) => {
                        if (!result || result.length <= 0) {
                            return;
                        }
                        let values = [];
                        result.map(resItem => values.push(resItem.text));
                        item.valueText = values.join(',');
                    }
                )
            }
            else {
                item.value = this._data[key];
                if (item.parameterTypeDesc !== 'help') {
                    return;
                }
                let helpId = (item.parameterType as StructureTypeRef).refId;
                this.http.getHelpData(helpId, [item.value]).subscribe(
                    (result: Array<HelpDataItem>) => {
                        if (!result || result.length <= 0) {
                            return;
                        }
                        item.valueText = result[0].text;
                    }
                )
            }
        })
    }

    private getParamDataItemsStructure() {
        if (!this.template || !this.template.params) {
            return;
        }
        this.paramDataItems = [];
        this.template.params.map(x => this.paramDataItems.push(x));
    }

    private covertParamDataToJson() {
        if (!this.paramDataItems || this.paramDataItems.length <= 0) {
            return null;
        }
        let object = new Object();
        this.paramDataItems.map((item: BaseParamData) => {
            if (this.isCollection(item)) {
                object[item.code] = [];
                let itemValues = item.value.split(',');
                if (!itemValues || itemValues.length <= 0) {
                    return;
                }
                itemValues.map(itemValue => object[item.code].push(itemValue));
                //å¸®å©nameèµå¼
                if (item.parameterTypeDesc === 'help') {
                    object[`${item.code}_name`] = [];
                    let itemTextValues = item.valueText.split(',');
                    if (!itemTextValues || itemTextValues.length <= 0) {
                        return;
                    }
                    itemTextValues.map(itemTextValue => object[`${item.code}_name`].push(itemTextValue));
                }
            }
            else {
                object[item.code] = item.value;
                //å¸®å©nameèµå¼
                if (item.parameterTypeDesc === 'help') {
                    object[`${item.code}_name`] = item.valueText;
                }
            }
        })
        return object;
    }

    close = () => {
        if (this.dialog) {
            this.dialog.close();
        }
        this.closeEmitter.emit();
    }

    confirm() {
        let jsonData = this.covertParamDataToJson();
        this.confirmEmitter.emit(jsonData);
    }

    private isCollection(param: any) {
        if (!param || !param.parameterType) {
            return false;
        }
        if (param.parameterType.getKind() === DataTypeConst.DEFAULT_COLLECTION) {
            return true;
        }
        else {
            return false;
        }
    }

}
