import { Component, OnInit, Input, OnChanges, SimpleChanges, Output, EventEmitter } from '@angular/core';
import { EnumerationType } from '@ecp-caf/common-structure';
import { UbccEventBusService } from '../../../service/event-bus-service';

@Component({
    selector: 'lib-dimension-dropdownlist-control',
    templateUrl: './dimension-dropdownlist-control.component.html',
    styleUrls: ['./dimension-dropdownlist-control.component.css']
})
export class DimensionDropdownlistControlComponent implements OnInit ,OnChanges{

    @Input() dimensionItem: any;

    @Output() valueChangeEmit:EventEmitter<any>=new EventEmitter();

    enumerationItems = [];

    isMultiSelect = false;

    isEnumInfoLoad: boolean = false;

    constructor(private ebsService: UbccEventBusService) { }

    ngOnInit() {
        this.dimensionItem = this.dimensionItem || {};
        if (!this.isEnumInfoLoad) {
            this.enumConfigInit();
        }
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (!this.isEnumInfoLoad) {
            this.enumConfigInit();
        }
    }

    enumConfigInit() {
        this.getEnumItems();
    }

    getEnumItems() {
        if (!this.dimensionItem || !this.dimensionItem.dataType) {
            return;
        }
        let enumType=this.dimensionItem.dataType as EnumerationType;
        this.get(enumType);
    }

    get(enumType: EnumerationType) {
        if (!enumType || !enumType.enumerationItems) {
            return;
        }
        enumType.enumerationItems.map(x => this.enumerationItems.push(x));
        this.isEnumInfoLoad = true;
    }

    valueChangeHandler($event) {
        this.dimensionItem.value=$event.value;
        this.ebsService.ebs.emit(this.dimensionItem);
    }

}
