import { Injectable } from '@angular/core';
import { ILookupHttpService, LookupGridResult } from '@farris/ui-lookup';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { SessionService } from '@ecp-caf/caf-common';
import { Observable, of } from 'rxjs';

@Injectable()
export class LookUpHttpService implements ILookupHttpService{

    httpHeaders = new HttpHeaders().set("sessionid", this.cache.getUserSessionId());

    constructor(private http: HttpClient, private cache: SessionService) {
        this.httpHeaders = new HttpHeaders();
        this.httpHeaders.set("sessionid", this.cache.getUserSessionId());
    } 
    
    getData(uri: string, params?: any): Observable<LookupGridResult> {
        if(uri.includes('{helpId}')){
            return of(null);
        }
        let queryParam=JSON.stringify(params);
        let url=`${uri}?queryParam=${encodeURIComponent(queryParam)}`;
        return this.http.get(url,{headers:this.httpHeaders});
    }
}