import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { DimensionItem, DataTypeExtensionConst } from '@gsp-cmp/ubcc-param-vo';
import { DataTypeConst } from '@ecp-caf/common-structure';

@Component({
    selector: 'lib-ubcc-param-dimension-ui',
    templateUrl: './ubcc-param-dimension-ui.component.html',
    styleUrls: ['./ubcc-param-dimension-ui.component.css'],
    encapsulation:ViewEncapsulation.None
})
export class UbccParamDimensionUiComponent implements OnInit {

    @Input() dimensionItemDatas: any=[];

    constructor() { }

    ngOnInit() {
       
    }

    getControlType(dimensionItem: DimensionItem) {
        if (!dimensionItem || !dimensionItem.dataType || !dimensionItem.dataType.getKind()) {
            return;
        }
        let kind = dimensionItem.dataType.getKind();
        switch (kind) {
            case DataTypeConst.DEFAULT_PRIMITIVE:
                return 'input';
            case DataTypeConst.DEFAULT_ENUMERATION:
                return 'dropdownlist';
            case DataTypeExtensionConst.HELP_TYPE:
                return 'lookup';
            default:
                return 'input';
        }
    }

}
