import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { UbccParamTemplate, ParameterGroup } from '@gsp-cmp/ubcc-param-vo';
import { DataTypeConst } from '@ecp-caf/common-structure';

@Component({
    selector: 'lib-ubcc-param-data-ui',
    templateUrl: './ubcc-param-data-ui.component.html',
    styleUrls: ['./ubcc-param-data-ui.component.css'],
    encapsulation:ViewEncapsulation.None
})
export class UbccParamDataUiComponent implements OnInit {

    @Input() template:UbccParamTemplate;

    @Input() paramDataItems:any=[];

    @Input() canEdit:boolean=true;

    constructor() { }

    ngOnInit() {
        this.template=this.template||new UbccParamTemplate();
        this.template.params=this.template.params||[];
        this.template.groups=this.template.groups||[];
    }

    getControlType(param:any){
        if(!param||!param.parameterType||!param.parameterTypeDesc){
            return;
        }
        switch(param.parameterTypeDesc){
            case 'string':
                return 'input';
            case 'bool':
                return 'checkbox';
            case 'double':
            case 'int':
                return 'number';
            case 'datetime':
                return 'date';
            case 'enum':
                return 'dropdownlist';
            case 'help':
                return 'lookup';
            default:
                return 'input';
        }
    }

    getParamItems(group:ParameterGroup){
        return this.paramDataItems.filter(x=>x.groupId===group.groupId);
    }

    getIndex(group:ParameterGroup,index:number){
        let currentGroupParams=this.getParamItems(group);
        let currentParam= this.paramDataItems.find(x=>x.id===currentGroupParams[index].id);
        let realIndex= this.paramDataItems.findIndex(x=>x.id===currentParam.id);
        return realIndex;
    }

    show(param:any){
        return param.visible||false;
    }

    isCollection(param:any){
        if(!param||!param.parameterType){
            return false;
        }
        if(param.parameterType.getKind()===DataTypeConst.DEFAULT_COLLECTION){
            return true;
        }
        else{
            return false;
        }
    }

}
