import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { DataTypeConst, EnumerationType, IDataType, CollectionType } from '@ecp-caf/common-structure';
import { UbccParamTemplate, StructureTypeRef, DataTypeExtensionConst } from '@gsp-cmp/ubcc-param-vo';

@Component({
    selector: 'lib-dropdownlist-control',
    templateUrl: './dropdownlist-control.component.html',
    styleUrls: ['./dropdownlist-control.component.css']
})
export class DropdownlistControlComponent implements OnInit, OnChanges {

    @Input() param: any;

    @Input() template: UbccParamTemplate;

    @Input() canEdit:boolean=true;

    enumerationItems = [];

    isMultiSelect = false;

    isEnumInfoLoad: boolean = false;

    constructor() { }

    ngOnInit() {
        this.param = this.param || {};
        this.template = this.template || new UbccParamTemplate();
        if (!this.isEnumInfoLoad) {
            this.enumConfigInit();
        }
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (!this.isEnumInfoLoad) {
            this.enumConfigInit();
        }
    }

    enumConfigInit() {
        this.isMultiSelect = this.isCollection();
        this.getEnumItems();
    }

    getEnumItems() {
        if (!this.param || !this.param.parameterType || !this.template.structuredTypes) {
            return;
        }
        if (this.param.parameterType.kind === DataTypeConst.DEFAULT_COLLECTION) {
            let elementType = this.getCollectionElementType(this.param.parameterType);
            this.get(elementType);
        }
        else if (this.param.parameterType.kind === DataTypeExtensionConst.UBCC_STRUCTURE_TYPE_REF) {
            let typeRef = this.param.parameterType as StructureTypeRef;
            this.get(typeRef);
        }
        else {
            throw new Error(`æä¸¾ä¸æ¯æçåæ°ç±»å${this.param.parameterType.kind}`);
        }
    }

    get(refType: StructureTypeRef) {
        let enumType = this.template.structuredTypes.find(x => x.id === refType.refId) as EnumerationType;
        if (!enumType || !enumType.enumerationItems) {
            return;
        }
        enumType.enumerationItems.map(x => this.enumerationItems.push(x));
        this.isEnumInfoLoad = true;
    }

    getCollectionElementType(dataType: IDataType) {
        let kind = dataType.getKind();
        switch (kind) {
            case DataTypeConst.DEFAULT_COLLECTION:
                let elementType = (dataType as CollectionType).elementType;
                return this.getCollectionElementType(elementType);
            default:
                return dataType;
        }
    }


    isCollection() {
        if (!this.param || !this.param.parameterType) {
            return false;
        }
        if (this.param.parameterType.getKind() === DataTypeConst.DEFAULT_COLLECTION) {
            return true;
        }
        else {
            return false;
        }
    }

    valueChangeHandler($event) {
        this.param.value=$event.value;
    }

    isReadonly(){
        if(!this.canEdit){
            return true;
        }
        else{
            return !this.param||!this.param.readonly?false:true;
        }
    }

    show(){
        return !this.param||!this.param.description?false:true;
    }

}
